  top: 0;
  z-index: 4;
}

.eventBox {
  z-index: 3;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
}

.box1 {
  display: flex;
  font-size: 26px;
  justify-content: center;
  flex-direction: column;
  background-color: #007bff;
  color: white;
