}

.event {
  padding: 1rem;
  background: linear-gradient(45deg, #0d6efd, #b400ff00);
  border-radius: 2px;
  color: white;
  font-size: 24px;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr;
}

.eventHeader {
  grid-row-start: 1;
  border-right: 3px solid white;
  grid-row-end: 4;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.show-details {
  width: 100%;
  height: 84%;
}

.eventButton {
  position: fixed;
  right: 0;
  background-color: transparent !important;
  color: white;
  font-size: 28px;
  cursor: pointer;
  border-color: transparent !important;
  transition-duration: 1s;
