.eventButtons {
  border-radius: 15px;
  padding: 6px;
  outline: 0;
  border: 1px solid transparent;
  color: rgb(0, 0, 0);
  background-color: #ebeef2;
  transition-duration: .5s;
}

.eventButtons:hover {
  transition-duration: .5s;
  border: 1px solid rgb(61, 61, 61);
}

.eventListAll {
  width: 75%;
  text-align: center;
  overflow-y: scroll;
  height: 60%;
  position: fixed;
  top: 26%;
}

.EventList {
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 2rem;
}

.event-description {
  padding: 20px;
  border-bottom: 4px solid white;
}

.eventLocation {
  border-bottom: 4px solid white;
}

.hide-details {
  background-color: #f92020 !important;
  border-color: #f92020 !important;
}

.show-details {
  height: 100%;
}

.show {
  height: 100%;
  left: 0;
  position: fixed;
  background-color: whitesmoke;
  top: 0;
  z-index: 4;
}

.eventBox {
  z-index: 3;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
}

.box1 {
  display: flex;
  font-size: 26px;
  justify-content: center;
  flex-direction: column;
  background-color: #007bff;
  color: white;
  padding: 4rem;
}

.box2 {
  display: flex;
  justify-content: center;
  flex-direction: column;
  font-size: 26px;
  background-color: #4a4242;
  color: white;
  padding: 4rem;
}

.box3 {
  display: flex;
  justify-content: space-around;
  font-size: 26px;
  background-color: whitesmoke;
}

.event {
  padding: 1rem;
  background: linear-gradient(45deg, #0d6efd, #b400ff00);
  border-radius: 2px;
  color: white;
  font-size: 24px;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr;
}

.eventHeader {
  grid-row-start: 1;
  border-right: 3px solid white;
  grid-row-end: 4;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.show-details {
  width: 100%;
  height: 84%;
}

.eventButton {
  position: fixed;
  right: 0;
  background-color: transparent !important;
  color: white;
  font-size: 28px;
  cursor: pointer;
  border-color: transparent !important;
  transition-duration: 1s;
}

.eventButton:hover {
  color: red;
  transform: scale(1.4);
  transition-duration: 1s;
}

.eventButton:active {
  opacity: .5;
}

.event-creator {
  border-bottom: 4px solid white;
}
